.admin-cover {
    display: flex;
    flex-direction: column;
    margin: 1em auto;
    width: 90%;
    height: 40em;
}

.create-content {
    width: 100%;
    height: 87.5%;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    box-sizing: border-box;
}

.create-cover {
    width: 42.5%;
    height: 90%;
    border-radius: 0.75em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.create-video {
    height: 15em;
    border-radius: 0.75em;
    box-shadow: 0 0.188em 1.550em -0.525em rgb(73, 71, 71);
    height: 90%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 45%;
}

.quiz-template {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100%;
    height: 85%;
    margin: 0 auto;
    border-radius: 0.75em;
    box-shadow: 0 0.188em 1.550em -0.525em rgb(73, 71, 71);
}

.admin-cover input {
    width: 60%;
    height: 10%;
    border: 0px solid transparent;
    border-radius: 0.9em;
    background-color: rgb(233, 232, 232);
    margin: 0 auto;
}

.quiz-template #q {
    width: 90%;
    height: 12.5%;
}

input:focus {
    outline: none;
}

#submit-link {
    display: flex;
    height: 10%;
    width: 90%;
    margin: 0 auto;
    padding: 1em;
    align-items: center;
}

#submit-link input {
    height: 60%;
}

#t {
    width: 27.5%;
    text-align: center;
}

.c {
    position: initial;
    height: 2em;
    width: 8em;
    margin: 0 auto;
}

.d {
    position: initial;
    height: 2.25em;
    width: 12em;
    margin: 0 auto;
    box-sizing: border-box;
    padding-top: 0.25em;
}

.steps {
    display: flex;
    justify-content: space-between;
    width: 92%;
    margin: 0 auto;
    color: grey;
}

.steps div:first-of-type {
    width: 49%;
}

.steps div:last-of-type {
    width: 46.5%;
}
